@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
.links-grid-items {
    font-family: 'Roboto', sans-serif;
    display: grid;
    grid-template-columns: 50px 1fr;
    margin: 5px;
}


.links-qr-code > svg {
    width: 50px;
    height: 50px;
}
.links-body {
    margin-left: 5px;
}

.links-short-url {
    font-size: 13px;
    font-weight: 700;
}

.links-url {
    font-size: 13px;
    font-weight: 500;
}

.links-date {
    font-size: 12px;
    font-weight: 400;
    opacity: 0.8;
}
.Placeholder  main-menu-text {
    padding: 0!important;
    margin: 0!important;
}
.PanelHeader__content > span {
    font-size: 16px!important;
}

